const CSSselect = require("css-select");
const htmlparser = require("htmlparser2");
const {selectAll, selectOne} = require("css-select");
const DomSerializer = require("dom-serializer").default;
const {getText, getAttributeValue} = require('./adapter');

module.exports = class HDHtmlParser {
    constructor() {
    }

    static load(html) {
        return new Promise((resolve, reject) => {
            try {
                let handler = new htmlparser.DomHandler((error, dom) => {
                    resolve(HDHtmlParser.registerFunction(dom));
                });
                let parser = new htmlparser.Parser(handler);
                parser.write(html);
                parser.end();
            } catch (e) {
                reject(e);
            }
        });
    }

    static querySelector(selector, dom) {
        const result = selectOne(selector, dom);
        if (!!result) {
            return HDHtmlParser.registerFunction(result);
        }
        return null;
    }

    static querySelectorAll(selector, dom) {
        let results = selectAll(selector, dom);
        if (results) {
            results = results.map(result => {
                if (!!result) {
                    return HDHtmlParser.registerFunction(result);
                }
                return null;
            }).filter(result => !!result);
            return results;
        }
        return null;
    }

    static getAttribute(attrName, dom) {
        try {
            return getAttributeValue(dom, attrName);
        } catch (e) {
            return null
        }
    }

    static getText(dom) {
        try {
            return getText(dom);
        } catch (e) {
            return null
        }
    }

    static getHtml(dom) {
        try {
            return DomSerializer(dom);
        } catch (e) {
            return null
        }
    }

    static registerFunction(dom) {
        dom.querySelector = (selector) => {
            return HDHtmlParser.querySelector(selector, dom);
        }
        dom.querySelectorAll = (selector) => {
            return HDHtmlParser.querySelectorAll(selector, dom);
        }
        dom.getAttribute = (attrName) => {
            return HDHtmlParser.getAttribute(attrName, dom);
        }
        dom.getText = () => {
            return HDHtmlParser.getText(dom);
        }
        dom.getHtml = () => {
            return HDHtmlParser.getHtml(dom);
        }
        return dom;
    }
}
